.contacto-container {
  font-family: "Glegoo", serif;
  height: 200px;
  color: whitesmoke;
}
.contacto-container a {
  color: rgb(0, 0, 0);
  text-decoration: none;
}
.contacto-container a:visited {
  color: rgb(0, 0, 0);
}
.contacto-container a:hover {
  font-weight: bold;
}
.contacto-container h3 {
  border: 1px solid black;
  background-color: rgb(1, 1, 77);
  margin-top: -40px;
  height: 80px;
}

.logo-contact {
  width: 40px;
  height: auto;
  margin: 10px;
  transition: opacity 0.3s ease-in-out;
}
.logo-contact:hover {
  opacity: 0.7;
}/*# sourceMappingURL=contacto-style.css.map */